'use client'
import Image from 'next/image';
import React, { useState, useEffect } from 'react';
import { FaArrowRight } from 'react-icons/fa';
import Link from 'next/link';
import ServiceContactForm from './ServiceContactForm';
import Navbar from './Navbar';

const ServicePageMain = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [isScrolled, setIsScrolled] = useState(false);
  const [showContactForm, setShowContactForm] = useState(false);
  const [showNavbar, setShowNavbar] = useState(true);
  
  const slides = [
    '/blue.jpg',
    '/digital.jpeg',
    '/Web-dev.webp',
    '/UI-UX.jpg',
  ];

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prevSlide) => 
        prevSlide === slides.length - 1 ? 0 : prevSlide + 1
      );
    }, 3000);

    const handleScroll = () => {
      setIsScrolled(window.scrollY > 0);
      setShowNavbar(window.scrollY < 100);
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      clearInterval(timer);
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <>
      <div className="relative min-h-[45vh] sm:min-h-[50vh] md:min-h-[60vh] lg:min-h-[65vh] w-full overflow-hidden">
        {/* Navbar Section */}
        <div className={`w-full transition-transform duration-300 ${showNavbar ? 'translate-y-0' : '-translate-y-full'}`}>
          <Navbar />
        </div>

        {/* Fixed Header with Path and Button */}
        <div className={`fixed top-0 w-full z-40 transition-all duration-300 ${isScrolled ? 'shadow-lg' : ''}`}>
          <div className="bg-white/90 h-14 sm:h-16 w-full px-4 sm:px-8 flex items-center justify-between hover:border-2 hover:border-black transition-all duration-300">
            <div className="flex items-center gap-1 sm:gap-2 text-sm sm:text-base text-gray-600">
              <Link href="/" className="hover:text-blue-600 hover:border-b-2 hover:border-blue-600 transition-all duration-300">Home</Link>
              <span>/</span>
              <span className="text-blue-600 hover:border-b-2 hover:border-blue-600 transition-all duration-300">Service</span>
            </div>
            <button 
              onClick={() => setShowContactForm(true)}
              className="bg-blue-600 text-white text-xs sm:text-sm font-semibold py-1.5 sm:py-2 px-3 sm:px-4 rounded-lg flex items-center gap-1 sm:gap-2 transition-all hover:bg-blue-700 hover:scale-105 hover:border hover:border-black"
            >
              Get in Touch
              <FaArrowRight className="text-sm sm:text-lg" />
            </button>
          </div>
        </div>

        {/* Slider Section */}
        <div className="absolute inset-0 w-full h-full">
          {slides.map((slide, index) => (
            <div
              key={index}
              className={`absolute inset-0 w-full h-full transition-opacity duration-1000 ease-in-out ${
                currentSlide === index ? 'opacity-100' : 'opacity-0'
              }`}
            >
              <Image
                src={slide}
                alt={`Slide ${index + 1}`}
                fill
                quality={100}
                className="object-cover object-center"
                sizes="(max-width: 480px) 100vw,
                       (max-width: 768px) 100vw,
                       (max-width: 1024px) 100vw,
                       (max-width: 1280px) 100vw,
                       100vw"
                priority={index === 0}
              />
            </div>
          ))}
        </div>
        
        {/* Services Title Box */}
        <div className="absolute bottom-0 translate-y-1/2 w-full px-4 sm:px-8 md:px-16 lg:px-24">
          <div className="container mx-auto">
            <div className="w-full max-w-[280px] sm:max-w-[400px] md:max-w-[500px] lg:max-w-[650px] hover:scale-105 transition-transform duration-300 group hover:shadow-xl">
              <div className="bg-blue-600/75 rounded-t-lg p-4 sm:p-6 md:p-8 min-h-[65px] sm:min-h-[75px] md:min-h-[85px] lg:min-h-[95px]">
                <h1 className="text-xl sm:text-3xl md:text-4xl lg:text-5xl font-bold text-white pl-2 sm:pl-4 group-hover:border-b-2 group-hover:border-black transition-all duration-300">
                  Services
                </h1>
              </div>
              <div className="bg-blue-500/75 rounded-b-lg p-4 sm:p-6 md:p-8 min-h-[65px] sm:min-h-[75px] md:min-h-[85px] lg:min-h-[95px]">
              </div>
            </div>
          </div>
        </div>
      </div>

      {showContactForm && <ServiceContactForm onClose={() => setShowContactForm(false)} />}
    </>
  );
};

export default ServicePageMain;
